.home{
  float: left;
  position: relative;
  width: 100%;

}
strong { font-weight: bold;}
a.btn:link,
a.btn:visited,
a.btn:hover,
a.btn:active {
    background: #66cc33;
    color: #fff;
    text-decoration: none;
    font-size: 22px;
    border-radius: 5px;
    border: none;
    padding: 10px 60px;
}

.home .container{
    position: relative;
    max-width: 1140px;
}
.home .section {
    position: relative;
    float: left;
    width: 100%;

}

.home .header{
    padding: 57px 0;

    background: url(https://modshow.tv/wp-content/uploads/2019/02/modshow-hero-bg.jpg) center top no-repeat;
}

.home .header img{
    margin-bottom: 120px;
}

.home .header .heroTitle{
    color: #fff;
    max-width: 720px;
    margin: 20px auto;
    font-weight: 600;
    font-size: 60px;
}

.home .header p{
    color: #fff;
    font-size: 26px;
    text-align: center;

}

.home .icons {
  background: #fff;
  padding: 57px 0;
}

.home .icons .column{
    position: relative;
    float: left;
    width: 30%;
    /* margin-right: 5.5%;*/
}
.home .icons .column.center{ margin: 0 5%; }

.home .icons .column h3{
  font-size: 30px;
  margin-bottom: 26px;

}

.home .icons .column p{
  font-size: 18px;
  line-height: 1.8;
}


.home .business{
    background: url(https://modshow.tv/wp-content/uploads/2019/05/Business-Man-Travelling-while-using-Modshow-TV.jpg) left center no-repeat;
    padding: 120px 0;
    background-blend-mode: overlay;
    background-color: rgba(255,255,255,0.63) !important;
}

.home  .section .container .columnHalf{
    position: relative;
    float: left;
    width: 50%;
    min-height: 1px;
}

.home  .section .container .columnHalf h3{
  font-size: 50px;
  color: #000;
  font-weight: 600;
  text-align: left;
}


.home  .section .container .columnHalf p{
    font-size: 18px;
    margin-bottom: 15px;
    line-height: 1.8;
    text-align: left;
}


.home .movie-crew{
    background: url(https://modshow.tv/wp-content/uploads/2019/05/Modshow-TV-Video-Production-Film-Shoot-Camera.jpg) center no-repeat;
    background-size: cover;
    padding: 120px 0;

}

.home .bed{
    background: url(https://modshow.tv/wp-content/uploads/2019/02/home-bg2.jpg) center no-repeat;
    background-size: cover;
    padding: 120px 0;

}

.home .lady{
  background: url(https://modshow.tv/wp-content/uploads/2019/05/Woman-Happy-Watching-Modshow-TV-in-Tablet.jpg) center no-repeat;
  background-size: cover;
  padding: 120px 0;
}

footer{
  position: relative;
  float: left;
  width: 100%;
  padding: 30px 0;
}

footer .container{
    text-align: center;
}

footer .container ul.menu { margin-top: 20px; }

footer .container ul.menu li{
    display: inline-block;
    padding: 0 15px;
}

footer .container ul.menu li.center{
    border-left: 1px solid #000;
    border-right: 1px solid #000;
}

footer .container ul.menu li a:link,
footer .container ul.menu li a:hover,
footer .container ul.menu li a:visited,
footer .container ul.menu li a:active{
    text-decoration: none;
    color: #000;
}

@media only screen and (max-width: 479px){
    .home .header img { margin-bottom: 20px; }
    .home .header .heroTitle{ font-size: 24px;  }
    .home .header p { font-size: 17px; padding: 0 10px;}
    .home .header p br { display: none; }

    .home .section .container { padding: 0 20px; }

    .home .icons .column,
    .home .section .container .columnHalf{ width: 100%; }
    .home .icons .column.center{ margin: 0 }

    .home .section .container .columnHalf { padding: 0; }
    .home .section .container .columnHalf h3 { font-size: 26px; text-align: center; }

    .home .business { padding: 30px 0; }
    .home .movie-crew { padding: 30px 0; background-position: right top; }
    .home .bed { padding: 30px 0; background-position: left top; }
    .home .lady { padding: 30px 0; background-position: right top; }
    footer .container ul.menu { width: 100%; padding: 0; }
}
